.fl-fb {
  color: #444; }

.fl-fb-ComponentsContainer {
  display: flex;
  flex-flow: column wrap;
  align-items: stretch; }

.fl-fb-FormComponent {
  position: relative;
  padding-bottom: 5px;
  margin: 5px 0;
  border-radius: 5px;
  transition: transform .2s ease-out, box-shadow .2s ease-out, background-color .2s ease-out; }
  .fl-fb-FormComponent:hover {
    background-color: #ebf4fc; }

.fl-fb-FormComponent-sidebar {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  min-height: 100%;
  padding: 5px 10px;
  border-radius: 0 5px 5px 0;
  text-align: center;
  background-color: #eee;
  transition: all .1s ease, visibility 0s, box-shadow .2s; }

.fl-fb-FormComponent-sidebar-btn {
  background-color: transparent;
  border: 0;
  opacity: .6; }
  .fl-fb-FormComponent-sidebar-btn:hover {
    opacity: 1; }

.fl-fb-FormComponent-configuration {
  background-color: #eee;
  width: 100%;
  padding: 15px;
  position: absolute;
  right: 0;
  bottom: 5px;
  z-index: 2;
  transform: translateY(100%);
  border-radius: 0 0 5px 5px;
  transition: all .2s ease, visibility 0s; }

.fl-fb-ControlBar {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
  padding-bottom: 10px; }

[class*='fl-fb-ControlBar-button'] {
  margin: 3px; }
